'use client';

import React, { useState, useEffect, useRef, useCallback } from 'react';
import dynamic from 'next/dynamic';
import ControlPanel from '@/components/ControlPanel';
import SearchBar from '@/components/SearchBar';
import { useBookSound } from '@/hooks/useBookSound';
import { AnimatePresence, motion } from 'framer-motion';
import { useMediaQuery } from 'usehooks-ts';
import { Search } from 'lucide-react';
import type { PDFDocumentProxy } from 'pdfjs-dist';

// Dynamically import FlipBook to avoid SSR issues with canvas/DOMMatrix
const FlipBook = dynamic(() => import('@/components/FlipBook'), {
  ssr: false,
  loading: () => (
    <div className="flex items-center justify-center text-white">
      <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-white mr-2" />
      Loading Viewer...
    </div>
  ),
});

export default function Home() {
  const [file] = useState<string | File>('/handbook.pdf');
  const [currentPage, setCurrentPage] = useState(1);
  const [numPages, setNumPages] = useState(0);
  const [isAutoRolling, setIsAutoRolling] = useState(false);
  const [isSinglePage, setIsSinglePage] = useState(false);
  const [isDarkMode, setIsDarkMode] = useState(true);

  const [showSearch, setShowSearch] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [searchMatches, setSearchMatches] = useState<number[]>([]);
  const [currentMatchIndex, setCurrentMatchIndex] = useState(-1);
  const [pdfDoc, setPdfDoc] = useState<PDFDocumentProxy | null>(null);
  const [isIndexing, setIsIndexing] = useState(false);
  const [isSearching, setIsSearching] = useState(false);

  const bookRef = useRef<any>(null);
  const wheelLockRef = useRef(0);
  const autoRollTimer = useRef<NodeJS.Timeout | null>(null);
  const textCacheRef = useRef<Record<number, string>>({});

  const isMobile = useMediaQuery('(max-width: 768px)');
  const { playFlip } = useBookSound();

  useEffect(() => {
    const prefersDark = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
    const initialDarkMode = document.documentElement.classList.contains('dark') || prefersDark;
    setIsDarkMode(initialDarkMode);
    document.documentElement.classList.toggle('dark', initialDarkMode);
  }, []);

  const toggleTheme = () => {
    setIsDarkMode((prevMode) => {
      const newMode = !prevMode;
      document.documentElement.classList.toggle('dark', newMode);
      return newMode;
    });
  };

  useEffect(() => {
    if (isMobile) {
      setIsSinglePage(true);
    }
  }, [isMobile]);

  const goToPage = useCallback(
    (page: number) => {
      const flipInstance = bookRef.current?.pageFlip?.();
      const totalPages = numPages || flipInstance?.getPageCount?.() || 1;
      const target = Math.min(Math.max(page, 1), totalPages);

      if (flipInstance) {
        flipInstance.flip(target - 1);
      }
      setCurrentPage(target);
    },
    [numPages],
  );

  const handlePageChange = useCallback(
    (page: number) => {
      setCurrentPage(page);
      playFlip();
    },
    [playFlip],
  );

  const handleDocumentLoadSuccess = useCallback((doc: PDFDocumentProxy) => {
    setNumPages(doc.numPages);
    setPdfDoc(doc);
    setCurrentPage(1);
    textCacheRef.current = {};
  }, []);

  const handlePrev = useCallback(() => {
    const flip = bookRef.current?.pageFlip?.();
    if (!flip) return;
    flip.flipPrev();
  }, []);

  const handleNext = useCallback(() => {
    const flip = bookRef.current?.pageFlip?.();
    if (!flip) return;
    flip.flipNext();
  }, []);

  const handlePageInput = (page: number) => goToPage(page);

  useEffect(() => {
    if (autoRollTimer.current) {
      clearInterval(autoRollTimer.current);
      autoRollTimer.current = null;
    }
    if (!isAutoRolling) return;

    autoRollTimer.current = setInterval(() => {
      const flip = bookRef.current?.pageFlip?.();
      if (!flip) return;
      if (flip.getCurrentPageIndex() < flip.getPageCount() - 1) {
        flip.flipNext();
      } else {
        setIsAutoRolling(false);
      }
    }, 2000);

    return () => {
      if (autoRollTimer.current) {
        clearInterval(autoRollTimer.current);
      }
    };
  }, [isAutoRolling]);

  const handleWheel = (e: React.WheelEvent<HTMLDivElement>) => {
    const now = Date.now();
    if (now - wheelLockRef.current < 450) return;

    const flip = bookRef.current?.pageFlip?.();
    if (!flip) return;

    if (e.deltaY > 0 && flip.getCurrentPageIndex() < flip.getPageCount() - 1) {
      flip.flipNext();
      wheelLockRef.current = now;
    } else if (e.deltaY < 0 && flip.getCurrentPageIndex() > 0) {
      flip.flipPrev();
      wheelLockRef.current = now;
    }
  };

  const handleReset = () => {
    setSearchQuery('');
    setSearchMatches([]);
    setCurrentMatchIndex(-1);
    setIsAutoRolling(false);
    goToPage(1);
  };

  const handleToggleView = () => {
    if (isMobile) return;
    setIsSinglePage((prev) => !prev);
  };

  useEffect(() => {
    if (!pdfDoc) return;
    let cancelled = false;
    textCacheRef.current = {};
    setIsIndexing(true);

    (async () => {
      for (let pageNum = 1; pageNum <= pdfDoc.numPages; pageNum++) {
        const page = await pdfDoc.getPage(pageNum);
        const textContent = await page.getTextContent();
        const text = textContent.items.map((item: any) => item.str || '').join(' ');
        textCacheRef.current[pageNum] = text;
        if (cancelled) return;
      }
      if (!cancelled) {
        setIsIndexing(false);
      }
    })().catch((err) => {
      if (!cancelled) {
        console.error('Failed to index PDF text', err);
        setIsIndexing(false);
      }
    });

    return () => {
      cancelled = true;
    };
  }, [pdfDoc]);

  const runSearch = useCallback(
    async (query: string) => {
      if (!pdfDoc) return [];
      const normalizedQuery = query.trim().toLowerCase();
      if (!normalizedQuery) return [];

      const matches: number[] = [];

      for (let pageNum = 1; pageNum <= pdfDoc.numPages; pageNum++) {
        let text = textCacheRef.current[pageNum];
        if (!text) {
          const page = await pdfDoc.getPage(pageNum);
          const textContent = await page.getTextContent();
          text = textContent.items.map((item: any) => item.str || '').join(' ');
          textCacheRef.current[pageNum] = text;
        }

        const lower = text.toLowerCase();
        let index = lower.indexOf(normalizedQuery);
        while (index !== -1) {
          matches.push(pageNum);
          index = lower.indexOf(normalizedQuery, index + normalizedQuery.length);
        }
      }

      return matches;
    },
    [pdfDoc],
  );

  useEffect(() => {
    if (!searchQuery.trim()) {
      setSearchMatches([]);
      setCurrentMatchIndex(-1);
      return;
    }
    if (!pdfDoc) return;

    let active = true;
    setIsSearching(true);

    runSearch(searchQuery)
      .then((matches) => {
        if (!active) return;
        setSearchMatches(matches);
        setCurrentMatchIndex(matches.length ? 0 : -1);
        if (matches.length) {
          goToPage(matches[0]);
        }
      })
      .catch((err) => {
        if (active) {
          console.error('Search failed', err);
        }
      })
      .finally(() => {
        if (active) {
          setIsSearching(false);
        }
      });

    return () => {
      active = false;
    };
  }, [searchQuery, pdfDoc, runSearch, goToPage]);

  const handleSearch = (query: string) => {
    setSearchQuery(query);
  };

  const handleSearchNext = () => {
    if (searchMatches.length === 0) return;
    const nextIndex = currentMatchIndex === -1 ? 0 : (currentMatchIndex + 1) % searchMatches.length;
    setCurrentMatchIndex(nextIndex);
    goToPage(searchMatches[nextIndex]);
  };

  const handleSearchPrev = () => {
    if (searchMatches.length === 0) return;
    const prevIndex =
      currentMatchIndex <= 0 ? searchMatches.length - 1 : (currentMatchIndex - 1) % searchMatches.length;
    setCurrentMatchIndex(prevIndex);
    goToPage(searchMatches[prevIndex]);
  };

  return (
    <main
      className={`min-h-screen relative overflow-hidden transition-colors duration-500 ${
        isDarkMode ? 'bg-slate-950 text-white' : 'bg-slate-50 text-slate-900'
      }`}
    >
      <div className="absolute inset-0 -z-10">
        <div
          className={`absolute inset-0 ${
            isDarkMode
              ? 'bg-gradient-to-br from-[#0c1224] via-[#0b1a34] to-[#050810]'
              : 'bg-gradient-to-br from-white via-[#f3f7ff] to-[#e6f0ff]'
          }`}
        />
        <div className="absolute -left-32 top-10 h-72 w-72 rounded-full bg-hts-secondary/20 blur-3xl" />
        <div className="absolute right-[-18%] bottom-[-10%] h-96 w-96 rounded-full bg-hts-primary/25 blur-[120px]" />
        <div className="absolute left-1/2 top-20 h-32 w-32 -translate-x-1/2 rounded-full bg-white/10 blur-3xl" />
      </div>

      <div className="relative h-screen flex flex-col gap-6 px-4 sm:px-10 pb-28 pt-10 max-w-7xl mx-auto">
        <motion.header
          initial={{ y: -30, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.65, ease: 'easeOut' }}
          className="w-full flex flex-col gap-4"
        >
          <div className="flex items-center justify-between gap-4">
            <div className="flex items-center gap-3">
              <div className="h-12 w-12 rounded-2xl bg-hts-secondary/20 border border-hts-secondary/40 flex items-center justify-center">
                <span className="text-lg font-bold text-hts-primary dark:text-hts-secondary">HTS</span>
              </div>
              <div>
                <p className="text-xs uppercase tracking-[0.28em] text-hts-secondary">Handbook</p>
                <h1 className="text-2xl sm:text-3xl font-semibold tracking-tight">Harbour Tech Solutions Â· 2025</h1>
              </div>
            </div>

            <button
              onClick={() => setShowSearch((open) => !open)}
              className="h-11 w-11 rounded-2xl bg-white/70 dark:bg-white/10 border border-white/50 dark:border-white/15 flex items-center justify-center text-hts-primary dark:text-hts-secondary hover:-translate-y-0.5 transition-all"
              title="Search PDF"
            >
              <Search className="w-5 h-5" />
            </button>
          </div>

          <AnimatePresence>
            {showSearch && (
              <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -10 }}
                className="max-w-2xl"
              >
                <SearchBar
                  onSearch={handleSearch}
                  onNext={handleSearchNext}
                  onPrev={handleSearchPrev}
                  currentMatch={currentMatchIndex + 1}
                  totalMatches={searchMatches.length}
                  onClose={() => setShowSearch(false)}
                  isSearching={isSearching}
                  isIndexing={isIndexing}
                />
              </motion.div>
            )}
          </AnimatePresence>
        </motion.header>

        <section className="relative flex-1 w-full">
          <div className="absolute inset-0 rounded-[28px] border border-white/15 dark:border-white/10 bg-white/70 dark:bg-white/5 backdrop-blur-[24px] shadow-[0_40px_120px_-50px_rgba(0,0,0,0.7)] overflow-hidden">
            <div className="absolute inset-0 pointer-events-none bg-gradient-to-b from-white/10 via-transparent to-black/10 dark:from-white/5" />

            <div
              className="relative h-full w-full"
              onWheel={handleWheel}
              onContextMenu={(e) => e.preventDefault()}
            >
              <FlipBook
                file={file}
                onPageChange={handlePageChange}
                onLoadSuccess={handleDocumentLoadSuccess}
                ref={bookRef}
                usePortrait={isSinglePage}
              />
            </div>
          </div>

          <div className="absolute left-1/2 -translate-x-1/2 -bottom-10 px-4 py-2 rounded-full bg-white/80 dark:bg-white/10 border border-white/40 dark:border-white/15 text-xs sm:text-sm tracking-[0.22em] uppercase text-gray-700 dark:text-gray-200 shadow-lg">
            Page {currentPage} of {numPages || '...'}
          </div>
        </section>
      </div>

      <ControlPanel
        currentPage={currentPage}
        numPages={numPages}
        onPrev={handlePrev}
        onNext={handleNext}
        onAutoRoll={() => setIsAutoRolling((v) => !v)}
        isAutoRolling={isAutoRolling}
        onReset={handleReset}
        onToggleView={handleToggleView}
        isSinglePage={isSinglePage}
        onToggleTheme={toggleTheme}
        isDarkMode={isDarkMode}
        onPageInput={handlePageInput}
      />
    </main>
  );
}
